import { Component, OnInit, ViewChild } from '@angular/core';
import { GeneralService } from '../common/general.service';
import {Song} from '../models/song';
// npm install @webcomponents/webcomponentsjs

@ViewChild('search') 

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  songs:Song[];
  song_results:Song[];
  filter_name:String;
  someRange:Number[] = [0,1];
  searchTerm:String = "";  
  rangeFilter:Boolean = false;
  date:any; 
  constructor(private http:GeneralService) { 
    this.date = new Date();
  }

  ngOnInit() {
  
  }

  setRange(event:any){
   if(this.rangeFilter) {
      this.songs = this.song_results.filter((elem)=>{
        return (elem.trackPrice>=event[0]  && elem.trackPrice<=event[1] );
      });
    }
  }
  
  changeFilter(event:any){
    this.filter_name = event.target.value;
  }

  get format() { return "fullDate";}
  
  get testFun() { return "this is test";}
  
  showRange(event:any){
    console.log(event);
  }

  setPriceFilter(){
    if(this.rangeFilter) this.setRange(this.someRange);
    else this.resetResults();
  }
  resetResults(){
    this.songs = this.song_results;
  }
  searchtext(event:any){  
    
    this.http.getData(event.target.value)
    .subscribe((response)=>{
      this.songs = this.song_results = response.results;
    });  
  
  }
}